import React, { useState, useContext, useEffect } from "react";
import { Loader } from "./Loader";
import axios from "axios";
import InfiniteScroll from "react-infinite-scroll-component";
import Nav from "./Nav";
import "./landing.css";
import GetAppIcon from "@material-ui/icons/GetApp";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from "@material-ui/icons/Info";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import Slide from "@material-ui/core/Slide";
import CloseIcon from '@material-ui/icons/Close';
import Container from '@material-ui/core/Container';
import { CustomThemeContext } from "../CustomThemeProvider";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const Home = () => {
  const { currentTheme, setTheme } = useContext(CustomThemeContext);
  const isDark = Boolean(currentTheme === 'dark')
  const [images, setImage] = useState([]);
  const [page, setPage] = useState(1);
  const [open, setOpen] = useState(false);
  const [currentImage, setCurrentImage] = useState();
  console.log("currentImage :: ", currentImage);
  const handleClickOpen = (data) => {
    setCurrentImage(data);
    setOpen(true);
  };

  const handleClose = () => {
    setCurrentImage(null);
    setOpen(false);
  };

  useEffect(() => {
    localStorage.clear();
    fetchImages();
  }, []);

  const fetchImages = (count = 20) => {
    setPage(1);
    const apiRoot = "https://api.unsplash.com";
    const accessKey = process.env.REACT_APP_ACCESSKEY;

    axios
      .get(
        `${apiRoot}/photos?client_id=${accessKey}&count=${count}&page=${page}`
      )
      .then((res) => {
        if (res && res.data) {
          const imageData = [];
          res.data.map((data, index) => {
            imageData.push({
              src: data.urls.small,
              width: data.width,
              height: data.height,
              index: index,
              extra: data,
            });
          });
          setImage([...images, ...imageData]);
          setPage(page + 1);
        }
      });
  };

  return (
    <div>
      <Nav />
      <Container style={{background: `${isDark ? "#555555" : "#fff"}`, maxWidth:"100%", paddingTop: "50px"}}>
        <div className="page-content">
          <InfiniteScroll
            dataLength={images.length}
            next={fetchImages}
            hasMore={true}
            loader={<Loader />}
          >
            <GridList cellHeight={350}>
              {images.map((img) => (
                <GridListTile key={img.key} className="imageTile" onClick={() => handleClickOpen(img)}>
                  <img src={img.src} alt={img.extra.alt_description} />
                  <GridListTileBar
                    title={img.extra.user.name}
                    actionIcon={
                      <IconButton>
                        <InfoIcon
                          style={{ color: "#fff" }}
                          onClick={() => handleClickOpen(img)}
                        />
                      </IconButton>
                    }
                  />
                </GridListTile>
              ))}
            </GridList>
          </InfiniteScroll>

          <Dialog
            open={open}
            TransitionComponent={Transition}
            keepMounted
            aria-labelledby="alert-dialog-slide-title"
            aria-describedby="alert-dialog-slide-description"
          >
            {currentImage && (
              <>
                <DialogContent>
                  <img src={currentImage.extra.urls.full} width="100%" height="100%" />
                </DialogContent>
                <DialogActions>
                  <a href={currentImage.extra.links.download + "?force=true"} download>
                    <IconButton className="actionBtn">
                      <GetAppIcon />
                    </IconButton>
                  </a>
                  <IconButton onClick={handleClose} className="actionBtn">
                    <CloseIcon />
                  </IconButton>
                </DialogActions>
              </>
            )}
          </Dialog>
        </div>
      </Container>
    </div>
  );
};

export default Home;
