import React, { useContext, useEffect } from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import ImageSearchIcon from "@material-ui/icons/ImageSearch";
import { Link } from "react-router-dom";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import InputBase from "@material-ui/core/InputBase";
import { fade, makeStyles } from "@material-ui/core/styles";
import SearchIcon from "@material-ui/icons/Search";
import PhotoAlbumIcon from "@material-ui/icons/PhotoAlbum";
import InstagramIcon from "@material-ui/icons/Instagram";
import { CustomThemeContext } from "../CustomThemeProvider";
import ShareIcon from '@material-ui/icons/Share';
import StarsIcon from '@material-ui/icons/Stars';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  logo: {
    marginRight: theme.spacing(2),
    color: "#fff",
    width: "30px",
    height: "30px",
  },
  instaBtn: {
    display: "none",
    color: "#fff",
    textDecoration: "none !important",
    background: "#f09433",
    background:
      "-moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%)",
    background:
      "-webkit-linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%)",
    background:
      "linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%)",
    filter:
      "progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 )",
    borderRadius: "50px !important",
    padding: "2px 30px",
    marginLeft: "",
    [theme.breakpoints.up("md")]: {
      display: "flex",
    },
  },
  listInstaBtn: {
    display: "flex",
    color: "#fff",
    textDecoration: "none !important",
    background: "#f09433",
    background:
      "-moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%)",
    background:
      "-webkit-linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%)",
    background:
      "linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%)",
    filter:
      "progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 )",
    borderRadius: "50px !important",
    padding: "2px 10px",
    width: "80%",
    margin: "0 auto",
    [theme.breakpoints.up("md")]: {
      display: "none",
    },
  },
  list: {
    width: 250,
    color: "#000",
  },
  fullList: {
    width: "auto",
  },
  title: {
    flexGrow: 1,
    display: "none",
    color: "#fff",
    textDecoration: "none !important",
    [theme.breakpoints.up("md")]: {
      display: "block",
    },
  },
  drawerHeader: {
    display: "flex",
    alignItems: "center",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: "flex-start",
  },
  search: {
    position: "relative",
    borderRadius: "22px",
    backgroundColor: fade(theme.palette.common.white, 1),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 1),
    },
    marginLeft: 0,
    width: "100%",
    [theme.breakpoints.up("md")]: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      width: "auto",
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    color: "#000",
  },
  inputRoot: {
    color: "#000",
    width: "100%"
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      width: "15ch",
      "&:focus": {
        width: "20ch",
      },
    },
  },
}));

export default function Navbar(props) {
  const { currentTheme, setTheme } = useContext(CustomThemeContext);
  const isDark = Boolean(currentTheme === "dark");

  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [query, setQuery] = React.useState(null);
  const open = Boolean(anchorEl);

  const [drawer, setDrawer] = React.useState(false);

  const handleDrawerOpen = () => {
    setDrawer(true);
  };

  const handleDrawerClose = () => {
    setDrawer(false);
  };

  const handleChange = (e) => {
    setQuery(e.target.value);
  };

  const handleKeyDown = (event) => {
    if (event.key === "Enter") {
      window.location.assign(`/search/${query}`);
    }
  };

  return (
    <div>
      <AppBar position="fixed">
        <Toolbar>
          <Link to="/">
            <PhotoAlbumIcon className={classes.logo} />
          </Link>
          <Link to="/" className={classes.title}>
            <Typography className={classes.title} variant="h5" noWrap>
              FotoMafia
            </Typography>
          </Link>
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search…"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              value={query}
              inputProps={{ "aria-label": "search" }}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
            />
          </div>
          <List>
            <a
              href="https://www.instagram.com/unseennature.ig/"
              target="blank"
              style={{ textDecoration: "none" }}
            >
              <ListItem button className={classes.instaBtn}>
                <ListItemIcon style={{ minWidth: "auto" }}>
                  <InstagramIcon style={{ color: "#fff" }} />
                </ListItemIcon>
                <ListItemText
                  primary="Follow on Instagram"
                  style={{ color: "#fff" }}
                />
              </ListItem>
            </a>
          </List>
          <IconButton
            edge="end"
            color="inherit"
            aria-label="open drawer"
            onClick={() => handleDrawerOpen()}
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer anchor="right" open={drawer} onClose={() => handleDrawerClose()}>
        <div
          className={classes.list}
          role="presentation"
          onClick={() => handleDrawerClose()}
          onKeyDown={() => handleDrawerClose()}
        >
          <div className={classes.drawerHeader}>
            <IconButton onClick={handleDrawerClose}>
              <ChevronRightIcon
                style={{ color: `${isDark ? "#fff" : "#000"}` }}
              />
            </IconButton>
            <h1 style={{ color: `${isDark ? "#fff" : "#000"}` }}>FotoMafia</h1>
          </div>
          <Divider />
          <List>
            <a href="/search/nature" style={{ textDecoration: "none" }}>
              <ListItem button>
                <ListItemIcon>
                  <ImageSearchIcon
                    style={{ color: `${isDark ? "#fff" : "#000"}` }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Nature"
                  style={{ color: `${isDark ? "#fff" : "#000"}` }}
                />
              </ListItem>
            </a>
          </List>
          <List>
            <a href="/search/people" style={{ textDecoration: "none" }}>
              <ListItem button>
                <ListItemIcon>
                  <ImageSearchIcon
                    style={{ color: `${isDark ? "#fff" : "#000"}` }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="People"
                  style={{ color: `${isDark ? "#fff" : "#000"}` }}
                />
              </ListItem>
            </a>
          </List>
          <List>
            <a href="/search/devotional" style={{ textDecoration: "none" }}>
              <ListItem button>
                <ListItemIcon>
                  <ImageSearchIcon
                    style={{ color: `${isDark ? "#fff" : "#000"}` }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Devotional Wallpaper"
                  style={{ color: `${isDark ? "#fff" : "#000"}` }}
                />
              </ListItem>
            </a>
          </List>
          <List>
            <a href="/search/health" style={{ textDecoration: "none" }}>
              <ListItem button>
                <ListItemIcon>
                  <ImageSearchIcon
                    style={{ color: `${isDark ? "#fff" : "#000"}` }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Health & Wellness"
                  style={{ color: `${isDark ? "#fff" : "#000"}` }}
                />
              </ListItem>
            </a>
          </List>
          <List>
            <a
              href="/search/street-Photography"
              style={{ textDecoration: "none" }}
            >
              <ListItem button>
                <ListItemIcon>
                  <ImageSearchIcon
                    style={{ color: `${isDark ? "#fff" : "#000"}` }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Street Photography"
                  style={{ color: `${isDark ? "#fff" : "#000"}` }}
                />
              </ListItem>
            </a>
          </List>
          <List>
            <a href="/search/fasion" style={{ textDecoration: "none" }}>
              <ListItem button>
                <ListItemIcon>
                  <ImageSearchIcon
                    style={{ color: `${isDark ? "#fff" : "#000"}` }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Fasion"
                  style={{ color: `${isDark ? "#fff" : "#000"}` }}
                />
              </ListItem>
            </a>
          </List>
          <List>
            <a href="#" style={{ textDecoration: "none" }}>
              <ListItem button>
                <ListItemIcon>
                  <ShareIcon
                    style={{ color: `${isDark ? "#fff" : "#000"}` }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Share App"
                  style={{ color: `${isDark ? "#fff" : "#000"}` }}
                />
              </ListItem>
            </a>
          </List>
          <List>
            <a href="#" style={{ textDecoration: "none" }}>
              <ListItem button>
                <ListItemIcon>
                  <StarsIcon
                    style={{ color: `${isDark ? "#fff" : "#000"}` }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Rate Us"
                  style={{ color: `${isDark ? "#fff" : "#000"}` }}
                />
              </ListItem>
            </a>
          </List>
          <List>
            <a
              href="https://www.instagram.com/unseennature.ig/"
              target="blank"
              style={{ textDecoration: "none" }}
            >
              <ListItem button className={classes.listInstaBtn}>
                <ListItemIcon style={{ minWidth: "auto" }}>
                  <InstagramIcon style={{ color: "#fff" }} />
                </ListItemIcon>
                <ListItemText
                  primary="Follow on Instagram"
                  style={{ color: "#fff" }}
                />
              </ListItem>
            </a>
          </List>
        </div>
      </Drawer>
    </div>
  );
}
